.mobile-menu {
  display: block;
}

.mobile-menu .main-logo {
  z-index: 102;
  background-color: #fff3;
  width: 70px;
  height: 70px;
  transition: background-color .5s ease-in-out;
  position: fixed;
  top: 1.5rem;
  left: 1.5rem;
}

.mobile-menu.active .main-logo {
  background-color: #ffffffe6;
}

.nav {
  visibility: hidden;
  z-index: 101;
  position: fixed;
  top: 10rem;
  left: 0;
}

.mobile-menu.active nav {
  visibility: visible;
}

.nav ul {
  z-index: 102;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  position: relative;
}

.nav a {
  opacity: 0;
  width: 100vw;
  margin: 0;
  padding: 2rem;
  font-size: 1.5rem;
  display: block;
}

.mobile-menu.active nav a {
  opacity: 1;
}

.nav-tgl {
  cursor: pointer;
  z-index: 102;
  text-align: center;
  background-color: #000c;
  border: none;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  padding: 0;
  line-height: .6;
  display: inline-block;
  position: fixed;
  top: 1.5rem;
  right: 1.5rem;
}

.nav-tgl > span {
  vertical-align: middle;
  background: #7c898b;
  border-radius: 1px;
  width: 34px;
  height: 2px;
  display: inline-block;
  position: relative;
}

.nav-tgl > span:before, .nav-tgl > span:after {
  content: "";
  background: #7c898b;
  border-radius: 1px;
  height: 2px;
  transition: all .2s;
  display: inline-block;
  position: absolute;
}

.nav-tgl > span:before {
  width: 28px;
  top: -11px;
  left: 3px;
}

.nav-tgl > span:after {
  width: 22px;
  top: 11px;
  left: 6px;
}

.nav-tgl:focus {
  outline: none;
}

.nav-tgl:hover > span:after, .nav-tgl:hover > span:before {
  width: 34px;
  left: 0;
}

.nav:before {
  content: "";
  clip-path: circle(30px at calc(100% - 65px) 65px);
  visibility: hidden;
  z-index: 100;
  background: #000c;
  width: 100vw;
  height: 100vh;
  transition: all .5s ease-in-out;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

.mobile-menu.active .nav:before {
  visibility: visible;
  clip-path: circle(100%);
}

.mobile-menu.active .nav-tgl > span {
  height: 0;
}

.mobile-menu.active .nav-tgl > span:after, .mobile-menu.active .nav-tgl > span:before {
  width: 34px;
  top: 0;
  left: 0;
}

.mobile-menu.active .nav-tgl > span:after {
  background: #fff;
  transform: rotate(-45deg);
}

.mobile-menu.active .nav-tgl > span:before {
  background: #fff;
  transform: rotate(45deg);
}

@media (width >= 600px) {
  .mobile-menu {
    display: none;
  }
}

.navbar {
  filter: drop-shadow(0 4px 4px #00000040);
  z-index: 99;
  visibility: hidden;
  background-color: #fff;
  justify-content: space-between;
  width: 100%;
  height: 47px;
  display: flex;
  position: fixed;
  top: 0;
}

@media (width >= 600px) {
  .navbar {
    visibility: visible;
  }
}

#navbar-fullscreen-links {
  z-index: 99;
  align-items: center;
  display: none;
}

#navbar-fullscreen-links a {
  color: #000;
  background-color: #fff;
  padding: 12px 28px;
  font-weight: 900;
  transition: background-color .4s ease-out;
}

#navbar-fullscreen-links a:hover {
  color: #fff;
  background-color: #333;
}

#navbar-fullscreen-links {
  display: flex;
}

.footer {
  background-color: #fff;
  width: 100%;
  min-height: 2rem;
}

.footer p {
  color: #000;
  margin: 0;
  padding-top: 1.5rem;
  padding-bottom: .5rem;
  font-weight: bold;
}

.footer img {
  cursor: pointer;
}

